.gameoverPage {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  height: 100%;
  width: 100%;
}

.gameoverPage__opaciy {
  background: #282c34;
  opacity: 0.3;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.gameoverPage__result-Wrapper {
  position: absolute;
  top: 50%;
  left: 50%;

  height: 50%;
  width: 50%;
  transform: translate(-50%, -50%);
  background-color: #25282e;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

button {
  margin: 0 auto;
  width: max-content;
  padding: 15px 30px;
  font-family: "Press Start 2P", cursive;
  color: #282c34;
  margin-top: 20px;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}

button:hover {
  transform: scale(1.03);
}
