.map {
  width: 100vw;
  height: 100vh;
  overflow: hidden;

  position: relative;

  font-family: "Press Start 2P", cursive;
}

.game-info {
  padding-top: 20px;
  display: flex;
  justify-content: space-around;
}

.game-info p {
  z-index: 1;
}

@keyframes floorUp {
  from {
    transform: translateY(100px);
  }
  to {
    transform: translateY(0);
  }
}

.floor {
  bottom: 0;
  position: absolute;
  width: 100%;
  height: 80px;
  background-color: #16171a;
  z-index: 1;

  animation: floorUp 1s forwards;
}
